/* ----------- iPhone 4 and 4S ----------- */

/* Portrait and Landscape */
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) and (-webkit-min-device-pixel-ratio: 2) {

    h1 {
        margin: 5% 0 5%;
        font-size: 4vh !important;
        -webkit-text-stroke: 2px $cc01;
    }

    h2 {
        margin: 5% 0 0;
        font-size: 3em !important;
    }

    h3 {
        font-size: 2.5em !important;
    }

    .btn {
        font-size: 3.5em !important;
    }

    .video_player {
        margin: 12% auto !important;
    }

    .input {
        position: relative;
        margin-bottom: 8%;

        .input_label {
            bottom: 100%;
            font-size: 2em;
        }

        .input_text {
            font-size: 2.5em;

            &::placeholder {
                color: #B0BEC5;
            }

            &:focus {
                outline: none;
                box-shadow: 0.2rem 0.8rem 1.6rem var(--colorTrans2);
            }
        }

        .input_num {
            font-size: 2.5em;
        }
    }

    .custom_select {
        font-size: 2.5em;

    }

    .quantity-input__modifier,
    .quantity-input__screen {
        margin: 5% 0;
        font-size: 2.2rem;
    }

    .quantity-input__modifier {
        padding: .7rem;
        width: 4rem;
    }

    //LYT SS1.1

    .ss1_1 {
        display: grid;
        grid-template-columns: 1fr !important;
        grid-template-rows: auto 1fr !important;
        gap: 0px 0px !important;
        grid-template-areas:
            "grd_left"
            "grd_right" !important;
        width: 100% !important;
        height: 100% !important;

        .grd_left {
            display: flex;
            padding-top: 5%;

            div {
                height: 90vw !important;
                width: 70vw !important;
                margin: 0 auto !important;
                padding: 0 !important;
            }
        }

        .grd_right {
            width: 100%;
            padding: 5% 5% 10%;

            .cont {
                width: 100% !important;
                height: auto;
                max-width: 1900px;

                h1 {
                    margin: 0 !important;
                    font-size: 4vh !important;
                }

                .flds {
                    padding: 8px;
                    float: left;

                    &:nth-child(1n) {
                        width: 25%;
                    }

                    &:nth-child(2n) {
                        width: 75%;
                    }
                }
            }

        }
    }

    //[END] LYT SS1.1

    //LYT SS1.3

    .ss1_3 {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 100%;
        grid-auto-columns: 1fr;
        grid-auto-rows: 1fr;
        gap: 0px 2%;
        grid-auto-flow: row;
        grid-template-areas:
            "grd_left grd_right";
        height: 100%;
        //max-width: 1028px;

        .grd_left {
            place-items: center;
            grid-area: grd_left;

            div {
                background-color: $cc01;
                height: 40vw;
                width: 40vw;
                max-width: 500px;
                max-height: 500px;
                border-radius: 5px;
                overflow: hidden;
                margin: 50px 0;
                float: right;
                border-radius: 50%;
                -moz-border-radius: 50%;
                -webkit-border-radius: 50%;
                box-shadow: 0.2rem 0.5rem 1.6rem var(--colorTrans0);
                background-color: $cc03;

                svg {
                    padding-top: 5vw;
                    width: 100%;
                    height: auto;

                    margin: 0 auto;
                }
            }
        }

        .grd_right {
            place-items: center;
            grid-area: grd_right;

            .cont {
                width: 100%;
                height: auto;
                max-width: 600px;
                padding: 10%;

                h1 {
                    font-size: $HFontSize_2;
                    padding: 50px 0;
                    margin-left: 8px;
                    text-align: left;
                }

                .form {

                    .flds {
                        padding: 8px 0 32px;
                    }
                }
            }

        }
    }

    //[END] LYT SS1.3

    //LYT SS1.4
    .ss1_4 {

        .grd_line2 {

            div {
                padding-top: 0vh !important;

                .card {

                    div {

                        svg {}
                    }

                    .quantity-input {
                        margin: 5% 0;
                        font-size: 4rem !important;
                    }
                }

            }
        }
    }

    //[END] LYT SS1.4

    //LYT SS1.5
    .ss1_5 {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: auto auto auto 1fr !important;
        grid-auto-columns: 1fr;
        grid-auto-rows: 1fr;
        gap: 0px 0px;
        grid-auto-flow: row;
        grid-template-areas:
            "grd_line1"
            "grd_line2"
            "grd_line3"
            "grd_line4";
        height: 100%;

        .grd_line1 {
            display: flex;
            justify-self: center;
            align-items: center;
            grid-area: grd_line1;

            h1 {
                margin: 0 0 2%;
                font-size: 4vh !important;
                padding: 0 !important;
                -webkit-text-stroke: 2px $cc01;
            }
        }

        .grd_line2 {
            clear: both;
            padding: 10% 0 0 !important;
            width: 100% !important;

            div {
                clear: both;
                padding: 0 2% !important;
                width: 100%;
                margin-bottom: 6% !important;

                label {
                    font-size: 2em !important;
                }
            }

        }

        .grd_line3 {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-wrap: nowrap;
            padding: 0 !important;
            grid-area: grd_line3;


        }

        .grd_line4 {
            clear: both;
            padding: 10% 0 0 !important;
            width: 100%;
            grid-area: grd_line4;

            div {
                clear: both;
                padding: 0 2%;
                width: 100% !important;

                &:nth-child(1n) {
                    margin: 0 !important;
                    margin-bottom: 2% !important;
                }

                &:nth-child(2n) {
                    margin: 0 !important;
                }

            }
        }
    }

    //[END] LYT SS1.5

    //LYT SS1.6
    .ss1_6 {
        display: grid;
        grid-template-columns: 1fr !important;
        grid-template-rows: auto 1fr !important;
        grid-auto-columns: 1fr !important;
        grid-auto-rows: 1fr !important;
        gap: 0px 0px !important;
        grid-auto-flow: row;
        grid-template-areas:
            "grd_left"
            "grd_right" !important;
        height: 100% !important;

        .grd_left {
            //background-color: aquamarine;
            grid-area: grd_left;

            .cont {
                width: 100%;
                height: 100%;
                padding: 0 2%;

                h1 {
                    text-align: center !important;
                }
            }
        }

        .grd_right {
            grid-area: grd_right;

            .cont {
                width: auto;
                height: auto;
                float: right;
                margin-right: 5vw;
                padding: 2vw;
                //background-color: blueviolet;

                .fathers {
                    position: relative;
                    width: auto;
                    height: auto;
                    //background-color: rgb(238, 164, 130);

                    div {
                        float: right;

                        &:nth-child(1n) {
                            //husband
                            height: auto;
                            color: $cc01;
                            margin-top: 0%;
                            margin-left: -40px;
                            //background-color: rgba(107, 128, 199, 0.555);
                        }

                        &:nth-child(2n) {
                            //wife
                            height: auto;
                            color: $cc01;
                            margin-top: 5%;
                            margin-left: -5%;
                            //background-color: rgba(199, 107, 194, 0.555);
                        }
                    }
                }

                .cont_kids {
                    position: absolute;
                    margin-top: 25vh;
                    margin-left: -15%;
                    margin-left: calc(-27% + 10%);

                    .kids_l2 {
                        height: auto;
                        background-color: rgb(224, 134, 205);

                        div {

                            &:nth-child(1n) {
                                height: auto;
                                float: left;
                                color: $cc01;
                                margin-top: -1%;
                                margin-left: -1%;
                                //background-color: rgb(0, 110, 161);

                                svg {
                                    height: 50%;
                                }

                            }

                            &:nth-child(2n) {
                                height: auto;
                                color: $cc01;
                                margin-top: -3%;
                                margin-left: -5%;
                                //background-color: rgb(0, 112, 9);

                                svg {
                                    height: 45%;
                                }
                            }

                            &:nth-child(3n) {
                                height: auto;
                                color: $cc01;
                                margin-top: 3%;
                                margin-left: -5%;
                                //background-color: rgb(0, 108, 112);

                                svg {
                                    height: 40%;
                                }
                            }

                            &:nth-child(4n) {
                                height: auto;
                                color: $cc01;
                                margin-top: -1%;
                                margin-left: -5%;
                                //background-color: rgb(0, 112, 9);

                                svg {
                                    height: 35%;
                                }
                            }

                            &:nth-child(5n) {
                                height: auto;
                                color: $cc01;
                                margin-left: -5%;
                                //background-color: rgb(0, 255, 34);

                                svg {
                                    height: 30%;
                                }
                            }

                            &:nth-child(6n) {
                                height: auto;
                                color: $cc01;
                                margin-left: -5%;
                                //background-color: rgb(121, 0, 81);

                                svg {
                                    height: 25%;
                                }
                            }
                        }
                    }
                }


            }
        }

    }

    //[END] LYT SS1.6
}

