:root {
  /* generic */
  --gutterSm: 0.8rem;
  --gutterMd: 1rem;
  --gutterLg: 1.2rem;
  --gutterXl: 1.5rem;
  --gutterXx: 1.8rem;
  --colorTrans0: #383D47;
  --colorTrans1: #FDE8D3;
  --colorTrans2: #F5CBA3;
  --colorTrans3: #E2745D;
  --colorTrans4: #DF603C;
  --colorTrans5: #ECB343;
  --colorTrans6: #83492E;
  --colorTrans7: #722022;
  --colorTrans8: #4A2415;
  --colorTrans9: #3E1D13;
  --fontSizeSm: 0.8rem;
  --fontSizeMd: 1rem;
  --fontSizeLg: 1.2rem;
  --fontSizeXl: 1.5rem;
  --fontSizeXx: 1.8rem;
  --fontSizeH1: 2vw;
  --marginTopBtmH1: 5vw;
  --paddingXxlH1:6.2vw;
  --paddingCanvas:-40vw;
  --fontSizeH2: 1vw;
  --marginTopBtmH2: 5vw;
  --lineHeightSm: 0.8;
  --lineHeightMd: 1;
  --transitionDuration: 300ms;
  --transitionTF: cubic-bezier(0.645, 0.045, 0.355, 1);
  /* floated labels */
  --inputPaddingV: var(--gutterMd);
  --inputPaddingH: var(--gutterLg);
  --inputFontSize: var(--fontSizeXl);
  --inputLineHeight: var(--lineHeightMd);
  --labelScaleFactor: 0.8;
  --labelDefaultPosY: 80%;
  --labelTransformedPosY: calc((var(--labelDefaultPosY)) - (var(--inputPaddingV) * var(--labelScaleFactor)) - (var(--inputFontSize) * var(--inputLineHeight)));
  --inputTransitionDuration: var(--transitionDuration);
  --inputTransitionTF: var(--transitionTF);
}

