  font-weight: normal;
  cursor: pointer;
}
.radio-item1 label:before {
  content: " ";
  display: inline-block;
  position: relative;
  top: 5px;
  margin: 0 5px 0 0;
  width: 20px;
  height: 20px;
  border-radius: 12px;
  border: 2px solid #3E1D13;
  background-color: transparent;
}
.radio-item1 input[type=radio]:checked + label:after {
  border-radius: 11px;
  width: 12px;
  height: 12px;
  position: absolute;
  top: 11px;
  left: 12px;
  content: " ";
  display: block;
  background: #3E1D13;
}

