.input {
  position: relative;
}
.input .input_label {
  display: block;
  position: absolute;
  bottom: 80%;
  left: 1.1rem;
  color: #383D47;
  font-size: var(--inputFontSize);
  font-weight: 500;
  line-height: var(--inputLineHeight);
  opacity: 0;
  transform: translate3d(0, var(--labelDefaultPosY), 0) scale(1);
  transform-origin: 0 0;
  transition: opacity var(--inputTransitionDuration) var(--inputTransitionTF), transform var(--inputTransitionDuration) var(--inputTransitionTF), visibility 0ms var(--inputTransitionDuration) var(--inputTransitionTF), z-index 0ms var(--inputTransitionDuration) var(--inputTransitionTF);
}
.input .input_text {
  display: block;
  margin: 0;
  padding: var(--inputPaddingV) var(--inputPaddingH);
  color: #383D47;
  width: 100%;
  font-size: var(--inputFontSize);
  font-weight: inherit;
  line-height: var(--inputLineHeight);
  border: none;
  border-radius: 3rem;
  transition: box-shadow var(--transitionDuration);
}
.input .input_text::placeholder {
  color: #B0BEC5;
}
.input .input_text:focus {
  outline: none;
  box-shadow: 0.2rem 0.8rem 1.6rem var(--colorTrans2);
}
.input .input_num {
  display: block;
  margin: 0;
  padding: var(--inputPaddingV) var(--inputPaddingH);
  color: inherit;
  width: 100%;
  font-size: var(--inputFontSize);
  font-weight: inherit;
  line-height: var(--inputLineHeight);
  border: none;
  border-radius: 3rem;
  transition: box-shadow var(--transitionDuration);
  text-align: center;
}
.input .input_num::-webkit-outer-spin-button, .input .input_num::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.input .input_num[type=number] {
  -moz-appearance: textfield;
}
.input .input_num::placeholder {
  color: #B0BEC5;
}
.input .input_num:focus {
  outline: none;
  box-shadow: 0.2rem 0.8rem 1.6rem var(--colorTrans2);
}

.input_text:placeholder-shown + .input_label {
  visibility: hidden;
  z-index: -1;
}
.input_text:not(:placeholder-shown) + .input_label, .input_text:focus:not(:placeholder-shown) + .input_label {
  visibility: visible;
  z-index: 1;
  opacity: 1;
  transform: translate3d(0, var(--labelTransformedPosY), 0) scale(var(--labelScaleFactor));
  transition: transform var(--inputTransitionDuration), visibility 0ms, z-index 0ms;
}

.input_num:placeholder-shown + .input_label {
  visibility: hidden;
  z-index: -1;
}
.input_num:not(:placeholder-shown) + .input_label, .input_num:focus:not(:placeholder-shown) + .input_label {
  visibility: visible;
  z-index: 1;
  opacity: 1;
  transform: translate3d(0, var(--labelTransformedPosY), 0) scale(var(--labelScaleFactor));
  transition: transform var(--inputTransitionDuration), visibility 0ms, z-index 0ms;
}

