  padding: 0.5rem;
  text-align: center;
  width: 100%;
  height: auto;
  background-color: #EAD0B2;
  border-radius: 1rem;
}

.time-slots {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  padding: 0.5rem;
}

.time-box {
  width: 14rem;
  max-width: 14rem;
  height: 14rem;
  max-height: 14rem;
  background-color: #D5B796;
  margin: 0.5rem;
}

.large-numbers {
  font-size: 2rem;
}

.flowboxes {
  display: inline-block;
  position: relative;
  height: 3rem;
  padding: 0 0.5rem;
  line-height: 3rem;
  margin-right: 5px;
  margin-left: -0.5rem;
  background-color: #FDE8D3;
  z-index: 200;
}

.flowboxes:after {
  content: "";
  border-top: 1px solid #FDE8D3;
  border-right: 1px solid #FDE8D3;
  width: 2rem;
  height: 2rem;
  position: absolute;
  right: -0.4rem;
  top: 0.25rem;
  background-color: #FDE8D3;
  z-index: -1;
  -webkit-transform: translate(10px, 4px) rotate(45deg);
  -moz-transform: translate(10px, 4px) rotate(45deg);
  -ms-transform: translate(10px, 4px) rotate(45deg);
  -o-transform: translate(10px, 4px) rotate(20deg);
  transform: translate(10px, 4px) rotate(45deg);
}

.disabled {
  background-color: gray;
  color: white;
}


