* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
body {
  width: 100vw;
  height: 100vh;
  font-family: 'Rubik', sans-serif;
  overflow: hidden;
  background-color: $cc01;
}

a {
  text-decoration: none !important;
}

.kenburns_top_right {
  display: block;
  -webkit-animation: kenburns_top_right 5s ease-out both;
  animation: kenburns_top_right 5s ease-out both;
}

/**
 * ----------------------------------------
 * animation kenburns_top_right
 * ----------------------------------------
 */
@-webkit-keyframes kenburns_top_right {
  0% {
    -webkit-transform: scale(1) translate(0, 0);
    transform: scale(1) translate(0, 0);
    -webkit-transform-origin: 84% 16%;
    transform-origin: 84% 16%;
  }

  100% {
    -webkit-transform: scale(1.25) translate(20px, -15px);
    transform: scale(1.25) translate(20px, -15px);
    -webkit-transform-origin: right top;
    transform-origin: right top;
  }
}

@keyframes kenburns_top_right {
  0% {
    -webkit-transform: scale(1) translate(0, 0);
    transform: scale(1) translate(0, 0);
    -webkit-transform-origin: 84% 16%;
    transform-origin: 84% 16%;
  }

  100% {
    -webkit-transform: scale(1.25) translate(20px, -15px);
    transform: scale(1.25) translate(20px, -15px);
    -webkit-transform-origin: right top;
    transform-origin: right top;
  }
}


////////////////////////////////////////////////////////////////

.svgSelected {
  -webkit-filter: drop-shadow(0px 0px 15px rgba(0, 0, 0, .7));
  filter: drop-shadow(0px 0px 15px rgba(0, 0, 0, .7));
}

.svgSelectedLight {
  -webkit-filter: drop-shadow(0px 0px 15px rgba(93, 70, 26, .7));
  filter: drop-shadow(0px 0px 15px rgba(93, 70, 26, .7));
}

.selectedLight {
  background-color: #ECB343 !important;
}

.btnSelected {
  color: #722022;
  background-color: transparent;
  border-color: #722022;
  box-shadow: none;
  font-weight: 800;

}

.ageError {
  color: var(--colorTrans4);
}

.summaryTable {
  margin: 0 auto;
  width: 75%;
  overflow-y: scroll;
  background-color: var(--colorTrans1);
}

/*DRAG n DROP*/

.droppableArea {
  min-width: 50px;
  min-height: 50px;
}

.multi-column {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.two-column,
.factor {
  width: 50%;
  max-width: 50%;
  min-width: 50%;
}

/* SS2 */

.goals-column {
  min-height: 7rem;
  min-width: 7rem;
}

.goalItem {
  height: 7rem;
  width: 7rem;
  margin: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  -webkit-user-select: none;
  /* Safari */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* IE10+/Edge */
  user-select: none;
  /* Standard */

  background-color: var(--colorTrans1);
  border: var(--colorTrans7) 5px solid;
  border-radius: 25%;
}

.family-goals {
  min-height: 12rem;
  border: 5px dashed var(--colorTrans7);
  padding: 0.5rem;
  border-radius: 1rem;
  background-color: #EAD0B2;
}

.family-goals>.goalItem {
  margin: 0;
}

.selected-goal {
  height: 12rem;
  width: 12rem;
}

.goal-icon {
  width: 6rem;
  height: auto;
}

.goal-icon.chosen {
  width: 3rem;
  height: auto;
}

.selected-goal>.goal-icon {
  width: 10rem;
  height: auto;
}

.income-goals>.goal-icon {
  width: 20rem;
  height: auto;
  border: 5px var(--colorTrans7) solid;
  margin: 0.5rem;
  border-radius: 15%;
  padding: 0.5rem;
  cursor: pointer;
  position: relative;
}

.goal-icon>.badge {
  width: 2rem;
  height: 2rem;
  border-radius: 100%;
  position: absolute;
  top: -0.5rem;
  right: -0.5rem;
  background-color: var(--colorTrans7);
  text-align: center;
  color: var(--colorTrans2);
  font-size: 1.5rem;
  vertical-align: middle;
}

.goals-title {
  text-align: center;
}

.goal-disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.goals-column-layout {
  display: flex;
  flex-direction: row;
  min-width: 100%;
  width: 100%;
  justify-content: center;
}

.goals-row-layout {
  min-height: 100% !important;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: space-between;
  align-items: center;
}

.customGoal {
  color: var(--colorTrans1);
  background-color: var(--colorTrans7);
  text-align: center;
}

.customGoal>h3 {
  color: var(--colorTrans1);
  width: 100%;
  overflow-wrap: break-word;
}

.goals-selection .customGoal {
  font-size: 2rem;
}

.cursor-pointer {
  cursor: pointer !important;
}

.goals-selection {
  min-width: 100%;
  width: 100%;
  min-height: 100%;
  height: 100%;
}

.availableGoals {
  min-height: 100%;
  height: 100%;
}

.center-content {
  display: flex;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.center-content-column {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.goals-table {
  width: 75%;
  margin: 2rem 0;
  font-size: 2rem;
}

.goals-col {
  padding: 0.5rem 2rem;
}

.disclaimer {

  margin-bottom: 3rem;
}

.goals-timeframe {
  padding: 0.5rem;
  text-align: center;
  width: 100%;
  height: auto;
  background-color: #EAD0B2;
  border-radius: 1rem;
}

.time-slots {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  padding: 0.5rem;
}

.time-box {
  width: 14rem;
  max-width: 14rem;
  height: 14rem;
  max-height: 14rem;
  background-color: #D5B796;
  margin: 0.5rem;
}

.large-numbers {
  font-size: 2rem;
}

.flowboxes {
  display: inline-block;
  position: relative;
  height: 3rem;
  padding: 0 0.5rem;
  line-height: 3rem;
  margin-right: 5px;
  margin-left: -0.5rem;
  background-color: #FDE8D3;
  z-index: 200;
}

.flowboxes:after {
  content: '';
  border-top: 1px solid #FDE8D3;
  border-right: 1px solid #FDE8D3;
  width: 2rem;
  height: 2rem;
  position: absolute;
  right: -0.4rem;
  top: 0.25rem;
  background-color: #FDE8D3;
  z-index: -1;

  -webkit-transform: translate(10px, 4px) rotate(45deg);
  -moz-transform: translate(10px, 4px) rotate(45deg);
  -ms-transform: translate(10px, 4px) rotate(45deg);
  -o-transform: translate(10px, 4px) rotate(20deg);
  transform: translate(10px, 4px) rotate(45deg);
}

.disabled {
  background-color: gray;
  color: white;
}